* {
    appearance: none;
    background: none transparent;
    border: 0 solid #000;
    color: inherit;
    font: inherit;
    margin: 0;
    padding: 0;
    text-decoration: none;
}
body {
    background-color: #fff;
    color: #000;
    font: 16px/20px "Noto Sans CJK JP", "Hiragino Sans", "Droid Sans", sans-serif;
}
main {
    padding: 4px 8px;
}
button {
    padding: 4px;
    margin: 4px;
    border: 1px solid #ccc;
}
